import React from 'react'
import {connect} from 'react-redux'
import action from "../store/action";
import { Form, Tabs, Button, Layout, Input, Menu, Radio, Icon, Tree, List, Modal } from 'antd';
import Controller from "../component/Controller"
import FileForm from "../component/FileForm"
import ControllerFunReturn from '../component/ControllerFunReturn'
import {withRouter} from 'react-router-dom';
import Qs from 'qs'
import {fetchProjectFileTreeApi} from "../api/projectConfig"
import jQuery from 'jquery'
import 'ztree'
import VoDomain from "../component/VoDomain";  //ztree
const $ = jQuery;

const { Header, Sider, Content } = Layout;
const TabPane = Tabs.TabPane;

class ProjectConfig extends React.Component {

    ztreeIndex = 0;
    projectId = 0;
    fileTree = null;
    rightClickNode; // 
    fileFormRef;
    openStats = {};
    state = {
        projectTree:[], // 
        collapsed: false,
        rightMenu: {left: '0px', top: '0px', display: 'none'},
    };

    controllerFunReturnPopPanel

    /* panels tabs */
    constructor(props, context) {
        super(props, context);

        /**  */
        const tabPanes = [];
        this.state = {
            ...this.state,
            activeKey: null,
            tabPanes,
        };

        let {location: {search}} = this.props,
            {projectId = 0} = Qs.parse(search.substr(1)) || {};

        this.projectId = projectId;

    }

    componentDidMount() {

        this.fetchProjectFileTree(this.projectId, 'Hierarchical', 0);

        /**
         *
         let that = this;
        // 
        $(document).bind("click", function (e)
        {
            console.info('------------click');
            var len = $((e.target || e.srcElement)).find(".right-menu-panel").length;
            if ( len === 0) {
                that.setState({...that.state, rightMenu:{display: 'none'}})
                // e.stopPropagation();//
            }
        });
         **/
    }

    fetchProjectFileTree = (projectId, packageType, moduleId) => {
        let fileTreeRet = fetchProjectFileTreeApi(projectId, packageType, moduleId);
        fileTreeRet.then((res) => {
            this.setState({...this.state, projectTree: eval(res.data.data)});
        })

    }

    onChange = (activeKey) => {
        this.setState({ ...this.state, activeKey });
    }

    onEdit = (targetKey, action) => {
        this[action](targetKey);
    }

    remove = (targetKey) => {
        let activeKey = this.state.activeKey;

        console.info('activeKey=' + activeKey+',targetKey=' + targetKey);

        let lastIndex;
        this.state.tabPanes.forEach((pane, i) => {
            if ('panel_'+pane.id === targetKey) {
                lastIndex = i - 1;
            }
        });
        const tabPanes = this.state.tabPanes.filter(pane => 'panel_'+pane.id !== targetKey);
        if (lastIndex >= 0 && activeKey === targetKey) {
            activeKey = 'panel_'+tabPanes[lastIndex].id;
        }
        this.setState({ ...this.state, tabPanes, activeKey });
    }


    addTab = (treeNode) => {
        console.info(JSON.stringify(treeNode));

        const tabPanes = this.state.tabPanes;
        const activeKey = 'panel_'+treeNode.id;
        var noOpen = true;
        for(var i=0;i<tabPanes.length; i++) {
            if(treeNode.id === tabPanes[i].id) {
                noOpen = false;
                break;
            }
        }
        if(noOpen) {
            tabPanes.push(treeNode);
            this.setState({ ...this.state, tabPanes, activeKey });
        }
    }

    showAddFilePanel = () =>{

        this.setState({...this.state, rightMenu:{display: 'none'}})

        console.info('--------------');
        this.fileFormRef.showFileFormPanel({fileType: this.rightClickNode.name}, (ret)=>{
            if(ret === 'close') {

            } else if(ret =='add') {
                // reload

            } else if(ret =='update') {

            }
        })

    }

    toggle = () => {
        this.setState({...this.state,
            collapsed: !this.state.collapsed,
        });
    }


    /* form */
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
            }
        });
    }

    render() {

        let zNodes = this.state.projectTree;
        for( var i=0; i<zNodes.length; i++ ) {

            var node = zNodes[i];
            var type = node.fileType;
            if( type === 'package' ) {
                node['icon'] = require("../static/images/package_obj.gif");
            } else if( type === 'folder' ) {
                node['icon'] = require("../static/images/source-folder.gif");
            } else {
                node['icon'] = require("../static/images/jcu_obj.gif");
            }

            // open status
            if( this.openStats[node.indentify] ) {
                node.open = true;
            }

            zNodes[i] = node;
        }

        var setting = {
            data : {
                simpleData : {
                    enable : true
                }
            },
            callback : {
                onClick : (event, treeId, treeNode)=>{
                    if(treeNode.fileType === 'package') {
                        return;
                    }

                    this.openStats[treeNode.getParentNode().indentify] = true;
                    this.addTab(treeNode);
                },
                onRightClick : (event, treeId, treeNode)=> { // 
                    console.info('right click ' + event.clientX + '--' + event.clientY)

                    if(treeNode.fileType === 'package') {
                        this.rightClickNode = treeNode;
                        this.setState({...this.state, rightMenu:{display: '', left:event.clientX+'px', top: (event.clientY - 32)+'px'}})
                    }
                }
            }
        };

        this.fileTree = $.fn.zTree.init($(this.refs.ztree), setting, zNodes);


        return (
            <Layout>
                <Sider
                    trigger={null}
                    collapsible
                    collapsed={this.state.collapsed}
                >
                    <div className="logo" >Project Name</div>


                    <div style={{background:'white', overflow:'auto', height:'100%', position: 'relative'}}>

                        <div className="ztree" ref="ztree" id={`ztree_${this.ztreeIndex++}`}> </div>

                        <div className="right-menu-panel" style={{padding: '10px', width: '140px', position: 'absolute',...this.state.rightMenu}}>
                        <List
                            style={{backgroundColor:'white'}}
                            size="small"
                            bordered
                        >
                            <List.Item><a onClick={this.showAddFilePanel}><Icon type="plus-circle" /></a></List.Item>
                            <List.Item><a><Icon type="close-circle" /></a></List.Item>
                        </List>
                        </div>

                    </div>


                </Sider>
                <Layout>
                    <Header style={{ background: '#fff', padding: 0 }}>
                        <Icon
                            className="trigger"
                            type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
                            onClick={this.toggle}
                        />
                    </Header>
                    <Content style={{ margin: '24px 16px', padding: 24, background: '#fff', minHeight: 280 }}>

                        {/*  */}
                        <div>
                            <Tabs
                                hideAdd
                                onChange={this.onChange}
                                activeKey={this.state.activeKey}
                                type="editable-card"
                                onEdit={this.onEdit}
                            >
                                {this.state.tabPanes.map(pane =>
                                    <TabPane tab={pane.name} key={'panel_'+pane.id}>
                                        {pane.fileType === 'controller'
                                            ?
                                            <Controller controllerFunReturnPopPanel={this.controllerFunReturnPopPanel} fileNode={pane} key={pane.id} ></Controller>
                                            :
                                            (pane.fileType === 'vo'
                                                ?
                                                <VoDomain projectId={this.projectId} fileNode={pane} key={pane.id}></VoDomain>
                                                :
                                                ''
                                            )
                                        }
                                    </TabPane>)}
                            </Tabs>
                        </div>
                    </Content>
                </Layout>

                {/*  */}
                <FileForm key="k_fileForm" onRef={(ref)=>{this.fileFormRef = ref}}></FileForm>


                {/*  */}
                <ControllerFunReturn projectId={this.projectId} key="controller_fun_return_key" onRef={(ref) => {this.controllerFunReturnPopPanel = ref }}></ControllerFunReturn>

            </Layout>
        );
    }
}

const _ProjectConfig = Form.create()(ProjectConfig);
const __ProjectConfig = withRouter(connect(state => ({...state.projectConfig}), action.projectConfig)(_ProjectConfig))

export default __ProjectConfig;