import React from "react";
import {Form, Layout, Select, List, Icon, Divider} from 'antd';
import {fetchDatabaseListApi, fetchTableListByDbIdApi} from '../api/databaseManager';

const {
    Header, Footer, Sider, Content,
} = Layout;

const Option = Select.Option;


const data = [
    'b_user',
    'crm_payhis',
    'crm_sys_user',
    'nl_calendar',
    'sys_user',
];

class DatabaseManager extends React.Component {

    state = {
        databaseList: [],
        tableList: []
    }
    constructor(props, context) {
        super(props, context);
    }

    componentDidMount() {
        fetchDatabaseListApi().then(res=>{
            this.setState({...this.state, databaseList: res.data.data})
        })
    }

    changeDatabase = (val) => {
        fetchTableListByDbIdApi(val).then(res=>{
            this.setState({...this.state, tableList: res.data.data})
        })
    }
    //


    render() {

        return (<div>
            <Layout>
                <Sider style={{backgroundColor: 'white'}}>
                    <Select style={{ width: '100%' }}
                            onChange = {this.changeDatabase}
                            dropdownRender={menu => (
                                <div>
                                    {menu}
                                    <Divider style={{ margin: '4px 0' }} />
                                    <div style={{ padding: '8px', cursor: 'pointer' }}>
                                        <Icon type="plus" /> 
                                    </div>
                                </div>
                            )}>

                        {this.state.databaseList.map(db=> <Option key={'_db_'+db.id} value={db.id}>{db.dbName}</Option>)}

                    </Select>
                    <h3>TABLES</h3>
                    <List className={"database-list"}
                          size="small"
                          dataSource={this.state.tableList}
                          renderItem={item => (<List.Item><span>{item.name}</span></List.Item>)}
                    />

                </Sider>
                <Content>Content</Content>
            </Layout>
        </div>)
    }
}

const _DatabaseManager = Form.create()(DatabaseManager);
export default _DatabaseManager;