import axios from 'axios';
axios.defaults.baseURL = 'http://47.90.62.38:9091/code_main';
axios.defaults.baseURL = 'http://localhost:9090/code_main'
axios.defaults.withCredentials = true;

// localStorage.setItem('TOKEN', token)

axios.interceptors.request.use(
    config =>{

        const {url} = config
        let token = localStorage.getItem('TOKEN');

        /**

        if(/^\/api\//.test(url) && !token && !window.location.href.indexOf('user') > -1){
            const { dispatch } = store
            dispatch(routerRedux.replace('/user/login')) // 
        }
        */

        if(token && url != '/api/login') {
            config.headers.Authorization = 'Bearer ' + token // token--['X-Token']key 
            config.params = {
                token: token,
                ...config.params
            }
        }
        return config;
    }, error => {
        console.info('---------------------'+error)
    }
)


axios.interceptors.response.use(
    res =>{

        //console.info(JSON.stringify(res))
        return res;
    },
    error =>{
        console.info('---------' + error + '|' + JSON.stringify(error));
        window.href = '#/login'
    })


export default axios;
