import React from 'react'
import {connect} from 'react-redux'
import action from '../store/action/index'
import { Form, Modal, Table, Input,  Spin, Icon, Tag, Divider } from 'antd';
import {Link} from 'react-router-dom';
import {genProjectApi} from '../api/projectManager';

const FormItem = Form.Item;

class ProjectManager extends React.Component {

    state = { visible: false, loading: false }

    constructor(props, context) {
        super(props, context);
    }

    componentDidMount() {

        let { fetchPageProject } = this.props;

        fetchPageProject(1, 22);

    }


    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
            }
        });
    }


    render() {

        let { projectList, showProjectDetail } = this.props;

        const rows = projectList.rows;

        /*
        artifactId: "test_pro"
contextPath: "/test"
createTime: 1490739991000
description: "crmtest"
groupId: "com.test"
id: 1
localPath: "/Users/leiwuluan/Desktop/test_project/"
name: "test_pro"
packageType: "standard"
updateTime: 1529745465000
version: "1.0"

         */

        const columns = [{
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            render: (text, record) => (
                <span>
                    <Link to={{ pathname:'/projectConfig', search: `?projectId=${record.id}` }} target="_blank">{text}</Link>
                </span>
            ),
        }, {
            title: 'ArtifactId',
            dataIndex: 'artifactId',
            key: 'artifactId',
        }, {
            title: 'GroupId',
            dataIndex: 'groupId',
            key: 'groupId',
        }, {
            title: 'version',
            key: 'version',
            dataIndex: 'version',
            render: text => <Tag color="blue" key={text}>{text}</Tag>,

        }, {
            title: 'Action',
            key: 'action',
            render: (text, record) => (
                <span>
                    <a href="javascript:;" onClick={()=>{
                        showProjectDetail(true)
                    }}></a>
                    <Divider type="vertical" />
                    <a href="javascript:;" onClick={()=>{

                        this.setState({...this.state, loading: true});
                        genProjectApi({id: record.id}).then(res=>{
                            this.setState({...this.state, loading: false});
                        })

                    }}></a>
                    <Divider type="vertical" />
                    <a href="javascript:;"></a>
                </span>
            ),
        }];


        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };

        return <div>

            <Spin spinning={this.state.loading} delay={500}>

            {/*  */}
            <Table columns={columns} dataSource={rows} />

            </Spin>
            {/*  */}
            <Modal
                title=""
                maskClosable={false}
                visible={this.props.showDetail}
                okText={""}
                onOk={()=>{
                    showProjectDetail(false);
                }}
                cancelText={""}
                onCancel={()=>{
                    showProjectDetail(false);
                }}
            >

                <Form onSubmit={this.handleSubmit}>
                    <FormItem
                        {...formItemLayout}
                        label="E-mail"
                    >
                        {getFieldDecorator('email', {
                            rules: [{
                                type: 'email', message: 'The input is not valid E-mail!',
                            }, {
                                required: true, message: 'Please input your E-mail!',
                            }],
                        })(
                            <Input />
                        )}
                    </FormItem>


                </Form>

            </Modal>

        </div>
    }
}

// 
const _ProjectManager = Form.create()(ProjectManager);

export default connect(state => ({...state.projectManager}), action.projectManager)(_ProjectManager);